<template>
  <div class="orderDetails">
    <div class="user-header">
      <a class="nav-item1" @click="orderDetailsBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="ordertails">订单详情</a>
    </div>
    <div class="orderDetailsMain">
      <div class="ordertailsTop">
        <!--<p><a>快递公司:{{details.shopping_name}}</a><span>快递号:{{details.send_order}}</span></p>-->
        <div class="send">
          <img src="../../assets/images/room/gc_17.png"/>
          <div v-if="!isSend">
            <a>备货中</a>
            <span>(预计3个工作日内发货)</span>
          </div>
          <div v-else class="sendOut">
            <a>已发货</a>
            <span>({{mailType}}:{{details.send_order}})</span>
          </div>
        </div>
        <div class="sendDetail" v-show="isSend">
          <div><img src="../../assets/images/thebar/wh_43.png" alt="">运输中</div>
          <p>物流运输物流运输物流运输物流运输物流运输物流运输物流运输物流运输物流运输物流运输</p><img class="getMore" src="../../assets/images/usercenter/gc_28.png" alt="">
        </div>
      </div>
      <div class="ordertailsC">
        <div class="orderHead">
          <a>订单号:{{details.order}}</a>
          <a>申请时间:{{timer}}</a>
        </div>
        <div class="orderBody" v-for="item in props">
          <img :src="item.icon_url"/>
          <span>
              <a>{{item.name}}{{item.remark}}</a>
              <a>×{{item.num}}</a>
            </span>
        </div>
        <div class="orderFoot">
          <ul>
            <li><p>运费：<a v-if="isFree">免邮</a><a v-else>{{mailPrice}} 元</a></p></li>
            <li><p>收货人：<a>{{address.consignee}}</a></p><span>{{address.mobile}}</span></li>
            <li><p>收货地址：<a>{{address.province + address.city + address.district + address.address}}</a></p></li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  export default {
    data() {
      return {
        details:[],
        props:[],
        address:[],
        mailType:'',
        timer:'',
        province:'',
        mailPrice:'',
        isFree:null,
        isSend:null,
      }
    },
    mounted:function () {
      var _this = this;
      var order_id = _this.$route.query.id;
      var totalNum = _this.$route.query.totalNum;
      $.ajax({
        type: 'GET',
        url: urlhost+"usercenter/packorderdetails?pack_order_id="+order_id,
        data$Type: "jsonp",
        data: {
        },
        success: function (response) {
          var mess = JSON.parse(response).message;
          _this.details = mess.order;

          //判断订单状态
          if( _this.details.status == 1 || _this.details.status == 4){
            this.isSend = false;
          }else{
            this.isSend = true;
          }
          var time = mess.order.created_at;
          var Int = parseInt(time);
          _this.timer = _this.date(Int);
          _this.props = mess.props;
          _this.address = mess.address;
          var province = mess.address.province;
          var order = mess.order.shopping_name;
          switch (order){
             case "1":
               _this.mailType = "圆通";
               break;
             case "2":
               _this.mailType = "中通";
               break;
             case "3":
               _this.mailType = "韵达";
               break;
             case "4":
               _this.mailType = "申通";
               break;
             case "5":
               _this.mailType = "天天";
               break;
             case "6":
               _this.mailType = "顺丰";
               break;
             case "7":
               _this.mailType = "邮政";
               break;
          };
          if( totalNum >= 3 ){
            _this.isFree = true;
          }else{
            _this.isFree = false;
            switch (province){
            case "新疆":
              _this.mailPrice = "10";
              break;
            case "西藏":
              _this.mailPrice = "10";
              break;
            case "内蒙古":
              _this.mailPrice = "10";
              break;
            default:
              _this.mailPrice = "6";
              break;
            }
          }
        },
      });
    },
    methods:{
      orderDetailsBack:function () {
        this.$router.go(-1);
      },
      date:function (data) {
        var date = new Date(data * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '/';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '/';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() < 10 ? '0'+(date.getMinutes()) : date.getMinutes();
        return Y+M+D+h+m;
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .orderDetails{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    background-color: #f2f2f2;
    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      line-height: n(80);
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1{
        position: absolute;
        left: 0.2rem;
        .game_1{
          width: n(50);
          height: n(50);
        }
      }
      .ordertails{
        font-size: 14px;
        font-weight: bold;
        width: 100%;
        display: inline-block;
        text-align: center;
      }
    }
    .orderDetailsMain{
      width: 95%;
      margin-left: 2.5%;
      margin-top: n(95);
      .ordertailsTop{
        margin-top: n(20);
        width: 100%;
        background: #fff;
        .send{
          padding:n(10) n(15);
          border-bottom: n(1) solid #f2f2f2;
          display: flex;
          img{
            width: n(50);
            height: n(50);
          }
          div{
            align-self: center;
            a{
              color: red;
            }

          }
        }
        .sendDetail{
          padding: n(16) n(30) n(26);
          position: relative;
          div{
            height: n(30);
            line-height: n(30);
            margin-bottom: n(5);
            img{
              height: n(30);
              width: n(30);
              margin-right: n(10);
            }
          }
          p{
            line-height: n(25);
            margin-right: n(20);
            margin-left: n(5);
          }
          .getMore{
            position: absolute;
            right: n(22);
            top: 25%;
          }
        }
      }
      .ordertailsC{
        margin-top: 10px;
        background: #ffffff;
        padding-left: 10px;
        padding-right: 10px;
        .orderHead{
          width: 100%;
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          height: n(60);
          border-bottom: 1px solid #f2f2f2;

        }
        .orderBody{
          height: n(130);
          border-bottom: 1px solid #f2f2f2;
          display: inline-flex;
          align-items: center;
          width: 100%;
          img{
            float: left;
            display: block;
            width: n(85);
            height: n(85);
            -webkit-border-radius: 5px;
            -moz-border-radius: 5px;
            border-radius: 5px;

          }
          span{
            margin-left: 13px;
            float: left;
            height: n(85);
            display: inline-flex;
            justify-content: center;
            flex-direction: column;
          }
        }
        .orderFoot{
          width: 100%;
          ul{
            li{
              width: 100%;
              padding: 5px 0 5px 0;
              display: inline-flex;
              justify-content: space-between;
              word-break: break-all;
              word-wrap: break-word;

            }

          }

        }
      }
    }
  }

</style>




